import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormControl, FormGroup, FormArray, FormBuilder, Validators } from '@angular/forms'
import { WebserviceService } from '../services/webservice.service'
import { environment } from 'src/environments/environment';
import { api } from '../services/api'
import { MessageService } from '../../@shared/message/message.service'

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

    formGroup: FormGroup
    env = environment
    api = api
    constructor(protected _message: MessageService, protected webservice: WebserviceService, protected fb: FormBuilder, private router: Router) { }

    ngOnInit() {
        this.initForm()
    }

    initForm() {
        this.formGroup = this.fb.group({
            username: ['', Validators.required],
            password: ['', Validators.required]
        })
    }

    onLogin(formGroup) {
        if(formGroup.invalid){
            // return
            this.router.navigate(['/dashboard']);
        }
        this.webservice.Post(`${this.env.baseUrl + this.api.login}`, formGroup.value).subscribe(res => {
            if (res && res.status == 'Success') {
                localStorage.setItem('token', `jwt ${res.user.token}`);
                localStorage.setItem('user', JSON.stringify(res.user))
                this.router.navigate(['/dashboard']);
            }
            this._message.succmessage(res.message)
        }, err => {
            this._message.errmessage(err)
        })
    }
}
